form {
  display: flex;
  flex-direction: column;
  width: 25%;
  margin: 0 auto;
  background-color: lightgray;
  padding: 10px;
}

label,
input {
  padding: 10px 0 10px 0;
  margin-top: 5px;
}

th,
td {
  border: 1px solid #ddd; 
  padding: 10px;
  text-align: left; 
}

table {
  border-collapse: collapse; 
  margin: 20px auto; 
  width: 80%; 
}

.h1 {
  text-align: center;
}

body {
  background-color: #f1f5f9;
}
.product-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  padding: 20px;
}

.product-item {
  background-color: #f7f7f7;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 0 1px rgba(0, 0, 0, 1);
}
